
import { defineComponent } from "vue";
let audio = new Audio();
audio.src = require("@/assets/music.mp3");
audio.autoplay = true;
audio.volume = 0.5;

export default defineComponent({
  name: "MusicPlayer",
  data() {
    return {
      status: "pause",
    };
  },
  methods: {
    play() {
      if (this.status === "play") {
        audio.play();
        this.status = "pause";
        return;
      } else {
        audio.pause();
        this.status = "play";
        return;
      }
    },
  },
});
